:root {
    --black:black;
    --border:6px solid red;
}

h3 {
    color: white;
}

.works {
    display:flex;
    margin-top: 60px;
}

.top,
.bottom {
    /*define height*/
    opacity: 50%;
    border-style: solid;
    border-color:cornsilk;
    border-width: 7px;
    margin-bottom: 24px;
    margin-left: 5px;
    position:relative;
    display: flex;
}

.group_a,
.group_b,
.individual_a,
.individual_b,
.individual_c,
.individual_d {
    position:absolute;
    left:0;
    top:50%;
    background-color: red;
    color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-left: 5px;
}

.group_a span,
.group_b span,
.individual_a span,
.individual_b span,
.individual_c span,
.individual_d span {
    font-size:large;
    font-weight:lighter;
}

.middle {
    display:flex;
}

.individual_a,
.individual_b,
.individual_c,
.individual_d {
    font-size:large;
    text-decoration:none;
}

.left,
.right {
    display: flex;
    flex-direction: column;
    margin-right: 5px;
}

.right {
    margin-right:0px;
    margin-left:5px;
}

.left section,
.right section {
    /*define height*/
    opacity: 50%;
    border-style: solid;
    border-color:cornsilk;
    border-width: 7px;
    margin-bottom: 24px;
    margin-left: 5px;
    position:relative;
    display:flex;
}

/* Firefox old*/ 

@-webkit-keyframes blink {
    0% {
        opacity:1;
    }
    50% {
        opacity:0.5;
    }
    100% {
        opacity:0.8;
    }
}
/* IE */ 
/* Opera and prob css3 final iteration */
@keyframes blink {
    0% {
        opacity:1;
    }
    50% {
        opacity:0.5;
    }
    100% {
        opacity:0.8;
    }
} 

.top:hover,
.bottom:hover,
.left section:hover,
.right section:hover { /* Firefox */
    -webkit-animation: blink normal 1s infinite ease-in-out; /* Webkit */ /* IE */
    animation: blink normal 1s infinite ease-in-out; /* Opera and prob css3 final iteration */
}

@media screen and (max-width: 992px) {

    .works {
        display:block;
    }

    .apps {
        margin-top: 20px;
    }

    .middle {
        flex-wrap: wrap;
    }

    .left {
        margin-right: 0px;
}

    .right {
        margin-left:0px;
}
}


body {
  padding-right: 60px;
}

.resume {
  height:100%;
  min-height: 100vh;
}

.container {
  width: 100%;
}

h2 {
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.col-lg-6 {
  flex: 0 0 auto;
  width: 380px;
  margin: 30px;
  
}

.resume .resume-title {
    font-size: 26px;
    font-weight: 700;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #45505b;
  }

  .resume .resume-item {
    padding: 0 0 20px 20px;
    margin-top: -2px;
    border-left: 2px solid #0563bb;
    position: relative;
  }

  .resume .resume-item h4 {
    line-height: 18px;
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    color: #0563bb;
    margin-bottom: 10px;
  }

  .resume .resume-item span {
    color: red;
    font-weight: bold;
    font-style: italic;
    text-decoration: underline;
    padding: 3px 3px;
    display: inline-block;
    margin-bottom: 10px;
  }

  .resume .resume-item ul {
    padding-left: 20px;
  }

  .resume .resume-item ul li {
    padding-bottom: 10px;
  }

  .resume .resume-item:last-child {
    padding-bottom: 0;
  }

  .resume .resume-item::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    left: -9px;
    top: 0;
    background: #fff;
    border: 2px solid #0563bb;
  }






